.navigation {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  right: 0;
  left: 0;
  padding: 35px 103px; }
  @media (max-width: 767px) {
    .navigation {
      padding: 30px 45px; } }
  @media (max-width: 319px) {
    .navigation {
      padding: 20px 15px; } }
  @media (max-width: 319px) {
    .navigation--closed .navigation__toggle {
      display: block; }
    .navigation--closed .navigation__toggle::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 24px;
      height: 2px;
      background-color: #f9fbfd;
      box-shadow: 0 6px 0 #f9fbfd, 0 12px 0 #f9fbfd; }
    .navigation--closed .navigation__list {
      display: none; }
    .navigation--opened {
      flex-wrap: wrap;
      row-gap: 33px;
      margin-left: 35px;
      padding: 20px 15px 303px;
      background-color: #f9fbfd; }
      .navigation--opened .navigation__icon-logo {
        fill: #011c40; }
      .navigation--opened .navigation__toggle {
        width: 15px;
        height: 15px;
        background-image: url("../img/svg/icon-close.svg");
        background-repeat: no-repeat;
        background-position: 0 0; }
      .navigation--opened .navigation__list {
        display: block; }
    .navigation--nojs {
      position: relative;
      flex-direction: column;
      align-items: flex-start;
      row-gap: 33px;
      margin-left: 35px;
      padding: 20px 15px 303px;
      background-color: #f9fbfd; }
      .navigation--nojs .navigation__icon-logo {
